<template>
  <Lay :route="route" :arr="arr" :url="url"/>
</template>

<script>
import Lay from '@/components/common/template';
import {gsald} from '@/config/well'
export default {
  props: ["route"],
  data () {
    return {
      url: '/data/query/formationdata',
      arr: gsald
    }
  },

  components: {
    Lay
  },

  mounted: function() {
  
  },

  methods: {
  }
}
</script>




